* {
  margin: 0%;
  padding: 0%;
  font-family: "Roboto", sans-serif;
  /* border:1px solid black; */
}

nav {
  background-color: aqua;
  color: black;
  padding: 20px;
  text-align: center;
  font-weight: bold;
  font-size: 1.2rem;
}

.container {
  background-color: rgb(252, 209, 216);
  width: 70%;
  margin: auto;
  min-height: 100vh;
  padding: 4vmax 8vmax;
  box-sizing: border-box;
}

.container > form {
  display: flex;
  flex-direction: column;
}

.container > form > input,
textarea,
button {
  padding: 20px;
  border: 1px solid rgba(0, 0, 0, 0.319);
  border-radius: 5px;
  margin: 5px 0px;
}

.container > h1 {
  text-align: center;
  margin: 5px;
  font-size: 2.5rem;
}

button {
  border: none;
  cursor: pointer;
  background-color: black;
  color: white;
  font-size: 1.2rem;
}

button:hover {
  background-color: rgba(0, 0, 0, 0.484);
}

.task {
  display: flex;
  background-color: white;
  margin: 20px 0;
  padding: 20px;
  border-radius: 5px;
  color: black;
  align-items: center;
}

.task > div {
  width: 100%;
}

.task span {
  color: rgb(114, 114, 114);
}

.task button {
  border-radius: 100%;
  height: 30px;
  width: 30px;
  font-size: 1.6rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: red;
  margin-left: 10px;
}

.task button:hover {
  background-color: rgba(255, 0, 0, 0.518);
}

.btn {
  width: 100%;
}

@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;700;900&display=swap");
